import { useEffect, useState ,useLayoutEffect} from 'react';
import { Link , useLocation, useParams } from 'react-router-dom';

const PopBoardView = () => {
	function menuPopClose(){//  
		document.querySelector("body").classList.remove("pop_on");
		const _pw = document.querySelectorAll("[class*='pop_wrap']");
		Array.from(_pw).forEach(function(v){
			//console.log(v);
			v.classList.remove("on");
			document.querySelector(".pop_wrap iframe").setAttribute("src","");
		});
	};

	return (
		<div className="pop_wrap">{/* //[D] : on   , body pop_on   */}
			<div className="pop_inwrap _sns">{/* //[D]    ( _sns, _spec )   . */}
				<div className="head">
					<h2 className="tit">22  ,   </h2>
					<p>
						<em>2021-08-21  <span className="ico_ytb"></span></em>
					</p>
				</div>
				{/* //head */}
				<div className="body">
					<iframe width="856" height="482" src="" title="YouTube video player" frameBorder="0"
							allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
							allowFullScreen></iframe>
				</div>
				{/* //body */}
				<button type="button" className="ibtn_close" onClick={menuPopClose}><span className="blind"></span></button>
			</div>
			{/* //pop_inwrap */}
		</div>
	)
}

export default PopBoardView;