import React, { useState } from 'react';
import { Link } from 'react-router-dom';

const PopSpec = (props) => {
	const [currentTab, setCurrentTab] = useState(0);
	const selectMenuHandler = (index) => {
		// parameter      ,  (event)  
		//      Tab Menu  .
		setCurrentTab(index);
	};
	function pop_close(){
		document.querySelector('body')?.classList.remove("pop_on"); // body pop_wrap   
		document.querySelector('.btn_drop')?.classList.remove("on");
		document.querySelector('.pop_wrap')?.classList.remove("on");
	}
	const Tabs = ({isTabs}) => {
		// console.log(isTabs.length);
		if(isTabs.length){
			return (<>
				<ul className="tab">
					{props.data.tabs?.map((el,index) => (
						<li key={index} className={index === currentTab ? "on" : "" }>
							<Link to={()=>false} onClick={() => selectMenuHandler(index)}>{el}</Link>
						</li>
					))}
				</ul>
			</>)
		}
		return (<></>)
	}
	return (
		<div className="pop_wrap">{/* //[D] : on   , body pop_on   */}
			<div className="pop_inwrap _spec">{/* //[D]    ( _sns, _spec )   . */}
				<div className="head"><h2 className="tit">{props.data.tit} <span>SPEC</span></h2></div>{/* //head */}
				<div className="body">
					<Tabs isTabs={props.data.tabs} />
					<div className="tabcont on"><img src={props.data.imgs[currentTab]} alt="" /></div>
				</div>{/* //body */}
				<button type="button" className="ibtn_close" onClick={pop_close}><span className="blind"></span></button>
			</div>
			{/* //pop_inwrap */}
		</div>
	)
}

export default PopSpec;